import React from "react";
import Slider from "react-slick";
import { Course } from "./commonComponent/Course";
import { CourseSkelethon } from "./CourseSkelethon";

const NextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <span className={className.replace("slick-next", "next-arrow")} style={style} onClick={onClick}>
            <i className="fas fa-chevron-right"></i>
        </span>
    );
};

const PrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
        <span className={className.replace("slick-prev", "prev-arrow")} style={style} onClick={onClick}>
            <i className="fas fa-chevron-left"></i>
        </span>
    );
};

export const CourseSlider = (props) => {
    const { courses } = props;
    const settings = {
        slidesToShow: 4,
        slidesToScroll: 4,
        dots: true,
        infinite: false,
        arrows: true,
        prevArrow: <PrevArrow />,
        nextArrow: <NextArrow />,
        responsive: [
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                },
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    dots: false,
                    arrows: false,
                },
            },
        ],
    };
    return (
        <div className="slick-course">
            {courses.length ? (
                <Slider {...settings}>
                    {courses.map((course) => {
                        return <Course key={course._id} data={course} />;
                    })}
                </Slider>
            ) : (
                <Slider {...settings}>
                    <CourseSkelethon />
                    <CourseSkelethon />
                    <CourseSkelethon />
                    <CourseSkelethon />
                </Slider>
            )}
        </div>
    );
};
