import React, { Component } from "react";
import { makeStyles, CardContent, CardActions, CardActionArea, Card } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";

export const CourseSkelethon = () => {
    const useStyle = makeStyles((theme) => ({
        root: {
            position: "relative",
            width: 312,
            margin: theme.spacing(2),
        },
        media: {
            height: 168,
            [theme.breakpoints.down("md")]: {
                height: 120,
            },
        },
        title: {
            marginTop: theme.spacing(6),
            height: 116,
            [theme.breakpoints.down("md")]: {
                marginTop: theme.spacing(3),
            },
        },
        uni: {
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
            overflow: "hidden",
        },
    }));
    const classes = useStyle();
    return (
        <Card className={classes.root}>
            <Skeleton variant='rect' animation="wave" className={classes.media} />
            <CardContent className={classes.title}>
                <Skeleton height={64} animation="wave" />
                <Skeleton height={20} animation="wave" width="50%" />
            </CardContent>
            <CardActions disableSpacing>
                <Skeleton variant="circle" animation="wave" height={36} width={36} style={{ padding: 12 }} />
                <Skeleton variant="circle" animation="wave" height={36} width={36} style={{ padding: 12 }} />
            </CardActions>
        </Card>
    );
};
